export default{
  data() {
    return {
      default_id: '',           // 默认收货地址ID
      addressList: [
        // {
        //   name: '羊羊羊',
        //   phone: '15555555555',
        //   region: '',
        //   detail: '',
        //   def: false
        // }, {
        //   name: '',
        //   phone: '',
        //   region: '',
        //   detail: '',
        //   def: false
        // }
      ]
    }
  },
  computed: {
    statusBarHeight() {
      return this.$store.state.statusBarHeight
    }
  },
  onLoad() {
    // // 获取地址列表
    // this.getAddress();
  },
  onShow() {
    // 获取地址列表
    this.getAddress();
  },
  methods: {
    // 获取收货地址列表
    getAddress() {
      let that= this
      that.$http({
        url: that.$api.addresslist,
        cb: (err, res) => {
          if(!err && res.code === 1) {
            this.addressList= res.data.list
            this.default_id= res.data.default_id
          } else if(res.code === 0) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '收货地址获取失败',
              icon: 'none'
            })
          }
        }
      })
    },
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    }, 
    addAction() {
      uni.navigateTo({
        url: 'addAddress'
      })
    },
    editAction(item) {
      let isDefault= this.default_id === item.address_id
      uni.navigateTo({
        url: 'addAddress?id=' + item.address_id + '&isDefault=' + isDefault
      })
    }
  }
}
